.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


/* logo */

@keyframes img-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main_img {
  height: 21vh;
  margin: 16px;
  padding: 16px;
 
}

@media (prefers-reduced-motion: no-preference) {
  .main_img {
    animation: img-logo-spin infinite 16s linear;
  }
}
html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

img {
  max-width: 100%;
}

body {
  font-family: "Inter", sans-serif;
  color: #8886fc;
  background: radial-gradient(circle, #1a2049 0%, #13162f 100%);
 
}






.items {
  display: flex;
  flex-wrap: wrap;
  margin-top: 16px;
  font-size: 13px;
  font-weight: 500;
  padding: 16px;
  
    
}


.item {
  flex: 0 1 calc(99% / 3);
  padding: 0;
  text-align: center;
  align-items: center;
}




